.location-weather {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 0px;
  margin-bottom: 60px;
}

.location-temperature {
  display: flex;
  flex: 1.8;
  font-size: 280px;
  height: 280px;
  font-family: MontserratBold;
  font-weight: 700;
  padding: 0;
  margin: 0;
  letter-spacing: -10px;
}

.location-degree {
  position: relative;
  z-index: 2;
  font-size: 100px;
  line-height: 215px;
  vertical-align: top;
  font-family: MontserratBold;
  font-weight: 700;
  padding: 0;
}

.location-icon {
  position: absolute;
  z-index: 2;
  top: -10px;
  width: 200px;
  height: 200px;
  margin-left: 20px;
  margin-top: -10px;
}

.location-details {
  flex: 1.2;
}

.location-details p {
  font-size: 22px;
  line-height: 22px;
  padding: 6px 0;
  margin: 0;
  font-weight: 700;
  letter-spacing: -1px;
}

.location-details span:nth-child(1) {
  padding-right: 10px;
}

.wind-units {
  text-transform: none;
}

