.location-forecast {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.nextday:nth-child(1),
.nextday:nth-child(2) {
  flex: 0.9;
}

.nextday:nth-child(3) {
  flex: 1.2;
}

.nextday p {
  font-size: 20px;
  font-weight: 700;
  padding: 0;
  margin: 0 0 10px;
}

p.day-temperature {
  font-size: 60px;
  padding: 0;
  margin: 0;
  vertical-align: top;
  font-weight: 700;
  letter-spacing: -2px;
}

p.day-temperature span {
  display: inline-block;
  vertical-align: bottom;
}

.nextday-icon {
  position: relative;
  z-index: 2;
  width: 80px;
  height: 98px;
  margin-left: 10px;
}

.nextday-icon svg {
  position: absolute;
  top: 0;
}

